namespace $ { export class $mol_string extends $mol_view {

	/// dom_name \input
	dom_name() {
		return "input"
	}

	/// enabled true
	enabled() {
		return true
	}

	/// hint \
	hint() {
		return ""
	}

	/// type?val \text
	@ $mol_mem()
	type( val? : any ) {
		return ( val !== void 0 ) ? val : "text"
	}

	/// attr * 
	/// 	placeholder <= hint 
	/// 	type <= type?val
	attr() {
		return $mol_merge_dict( super.attr() , {
			"placeholder" : ()=> <any> this.hint() ,
			"type" : ()=> <any> this.type() ,
		} )
	}

	/// disabled false
	disabled() {
		return false
	}

	/// value?val \
	@ $mol_mem()
	value( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// value_changed?val <=> value?val
	@ $mol_mem()
	value_changed( val? : any ) {
		return this.value( val )
	}

	/// field * 
	/// 	disabled <= disabled 
	/// 	value <= value_changed?val
	field() {
		return $mol_merge_dict( super.field() , {
			"disabled" : ()=> <any> this.disabled() ,
			"value" : ()=> <any> this.value_changed() ,
		} )
	}

	/// event_change?event null
	@ $mol_mem()
	event_change( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// event * input?event <=> event_change?event
	event() {
		return $mol_merge_dict( super.event() , {
			"input" : ( event? : any )=> <any> this.event_change( event ) ,
		} )
	}

} }

