namespace $ { export class $mol_link extends $mol_view {

	/// minimal_height 36
	minimal_height() {
		return 36
	}

	/// dom_name \a
	dom_name() {
		return "a"
	}

	/// uri \
	uri() {
		return ""
	}

	/// current false
	current() {
		return false
	}

	/// attr * 
	/// 	href <= uri 
	/// 	mol_link_current <= current
	attr() {
		return $mol_merge_dict( super.attr() , {
			"href" : ()=> <any> this.uri() ,
			"mol_link_current" : ()=> <any> this.current() ,
		} )
	}

	/// arg *
	arg() {
		return ({
		})
	}

} }

